
























.toonui {
}

.toonui-indicator {
}

.toonui-indicator-wrapper {
  top: 50%;
  left: 50%;
  position: fixed;
      -webkit-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  box-sizing: border-box;
  text-align: center;
}

.toonui-indicator-text {
  display: block;
  color: #fff;
  text-align: center;
  margin-top: 10px;
  font-size: 16px;
  font-weight: 400;
}

.toonui-indicator-spin {
  display: inline-block;
  text-align: center;
}

.toonui-indicator-mask {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  opacity: 0;
  background: transparent;
}

.toonui-indicator-transition {
  -webkit-transition: opacity .2s linear;
  transition: opacity .2s linear;
}

.toonui-indicator-enter, .toonui-indicator-leave {
  opacity: 0;
}
.toonui-spinner-snake {
  -webkit-animation: toonui-spinner-rotate 0.8s infinite linear;
          animation: toonui-spinner-rotate 0.8s infinite linear;
  border: 4px solid transparent;
  border-radius: 50%;
  border-top-color: #ccc;
  border-left-color: #ccc;
  border-bottom-color: #ccc;
  width:32px;
  height:32px;
}

@-webkit-keyframes toonui-spinner-rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes toonui-spinner-rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



