




































.m-button{
  margin: 10px auto;
  position: relative;
  width: 100%;
  border: none;
  text-align: center;
  border-radius: 4px;
  outline: none
}
.m-button:active:not(.disable){
  filter: alpha(opacity=60);
  opacity: 0.6
}
.m-button--default{
  background-color: #3395ff;
  color: #fff;
  height: 44px;
  font-size: 17px
}
.m-button--blue{
  background-color: #3395ff;
  color: #fff;
  height: 39px;
  font-size: 17px
}
.m-button--red{
  background-color: #FF5A5F;
  color: #fff;
  height: 39px;
  font-size: 17px
}
.m-button--plain--default{
  border-radius: 22px;
  color: #007aff;
  position: relative
}
.m-button--plain--default:after{
  content: " ";
  width: 200%;
  height: 200%;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #3395ff;
      -webkit-transform: scale(.5);
      transform: scale(.5);
      -webkit-transform-origin: 0 0;
      transform-origin: 0 0;
  border-radius: 44px
}
.disable{
  background-color: #dddee3;  
  color: #fff
}
.disable:after{
  border: none
}
.small {
  font-size: 16.5px;
}



