  position: relative;
}
.tab-container .tn-tabbar {
  background-image: -webkit-l inear-gradient(top,#d9d9d9,#d9d9d9 50%,transparent 0);
  background-image: -webkit-linear-gradient(top,#d9d9d9,#d9d9d9 50%,transparent 0);
  background-image: linear-gradient(180deg,#d9d9d9,#d9d9d9 50%,transparent 0);
  background-size: 100% 1px;
  background-repeat: no-repeat;
  background-position: 0 0;
  /*position: relative;*/
  background-color: #fafafa;
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  /*right: 0;*/
  /*bottom: 0;*/
  left: 0;
  /*position: absolute;*/
  text-align: center;
}
.tab-container .tn-tabbar .is-fixed {
  right: 0;
  bottom: 0;
  position: fixed;
  z-index: 1;
}
.tab-container .tn-tab-item {
  display: block;
  -ms-flex: 1;
  -webkit-box-flex: 1;
          flex: 1;
  text-decoration: none;
  height:44px;
  line-height:44px;
}
.tab-container .tn-tab-item .selected{
  padding:0px 10px;
  border-bottom:1px solid #007AFF;
  color:#000;
  display:inline-block;
  height:44px;
}
.tab-container .tn-tab-item-label {
  color: inherit;
  font-size: 12px;
  line-height: 1;
}
.tab-container .tn-tabbar>.tn-tab-item.is-selected>.tn-tab-item-tit {
  border-bottom:1px solid #007AFF;
  color: #000;
  padding:0px 10px;
  display:inline-block;
  height:44px;
}
.tab-container .tn-tab-container-item {
  color:#000;
  padding: 10px;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  width: 100%;
  /*position: absolute;*/
  top: 75px;
  /*z-index:99;*/
}



